/* src/components/portfolio/PortfolioSummary.css */
.portfolio-summary {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1.5rem;
  margin-bottom: 2rem;
}

.summary-card {
  background: white;
  padding: 1.5rem;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  display: flex;
  align-items: center;
  gap: 1rem;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.summary-card:hover {
  transform: translateY(-3px);
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.15);
}

.summary-card.primary {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
}

.summary-icon {
  font-size: 2.5rem;
}

.summary-content h3 {
  font-size: 0.85rem;
  font-weight: 500;
  text-transform: uppercase;
  opacity: 0.8;
  margin-bottom: 0.5rem;
}

.summary-card.primary h3 {
  color: rgba(255, 255, 255, 0.9);
}

.value-large {
  font-size: 2rem;
  font-weight: 700;
  margin: 0;
}

.value {
  font-size: 1.5rem;
  font-weight: 700;
  margin: 0;
  color: #2d3748;
}

.summary-card.primary .value,
.summary-card.primary .value-large {
  color: white;
}

.sub-value {
  font-size: 0.85rem;
  margin-top: 0.25rem;
  opacity: 0.7;
}

.value.positive,
.sub-value.positive {
  color: #48bb78;
}

.value.negative,
.sub-value.negative {
  color: #f56565;
}

@media (max-width: 768px) {
  .portfolio-summary {
    grid-template-columns: 1fr;
  }
}