/* src/components/portfolio/TransactionHistory.css */
.transaction-history {
  background: white;
  border-radius: 12px;
  padding: 1.5rem;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.transaction-history.empty {
  padding: 3rem;
}

.empty-state {
  text-align: center;
  color: #718096;
}

.empty-icon {
  font-size: 4rem;
  margin-bottom: 1rem;
}

.empty-state h3 {
  color: #2d3748;
  margin-bottom: 0.5rem;
}

.history-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
  padding-bottom: 1rem;
  border-bottom: 2px solid #e2e8f0;
  flex-wrap: wrap;
  gap: 1rem;
}

.history-header h3 {
  color: #2d3748;
  font-size: 1.3rem;
  margin: 0;
}

/* Filter Buttons */
.filter-buttons {
  display: flex;
  gap: 0.5rem;
}

.filter-buttons button {
  padding: 0.5rem 1rem;
  border: 2px solid #e2e8f0;
  background: white;
  color: #718096;
  border-radius: 8px;
  font-weight: 600;
  font-size: 0.85rem;
  cursor: pointer;
  transition: all 0.3s ease;
}

.filter-buttons button:hover {
  border-color: #cbd5e0;
}

.filter-buttons button.active {
  background: #667eea;
  color: white;
  border-color: #667eea;
}

.filter-buttons button.buy.active {
  background: #48bb78;
  border-color: #48bb78;
}

.filter-buttons button.sell.active {
  background: #f56565;
  border-color: #f56565;
}

/* Transactions List */
.transactions-list {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.transaction-item {
  display: grid;
  grid-template-columns: auto 1fr auto;
  gap: 1rem;
  padding: 1.25rem;
  border-radius: 12px;
  border: 2px solid #e2e8f0;
  transition: all 0.3s ease;
  align-items: center;
}

.transaction-item:hover {
  border-color: #cbd5e0;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
}

.transaction-item.buy {
  border-left: 4px solid #48bb78;
}

.transaction-item.sell {
  border-left: 4px solid #f56565;
}

.transaction-icon {
  font-size: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Transaction Details */
.transaction-details {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.transaction-main {
  display: flex;
  align-items: center;
  gap: 0.75rem;
}

.transaction-type {
  font-weight: 700;
  font-size: 0.8rem;
  padding: 0.25rem 0.75rem;
  border-radius: 6px;
  text-transform: uppercase;
}

.transaction-type.buy {
  background: rgba(72, 187, 120, 0.15);
  color: #22543d;
}

.transaction-type.sell {
  background: rgba(245, 101, 101, 0.15);
  color: #742a2a;
}

.transaction-coin {
  font-weight: 700;
  color: #2d3748;
  font-size: 1.1rem;
}

.transaction-meta {
  display: flex;
  align-items: center;
  gap: 1rem;
  flex-wrap: wrap;
}

.transaction-date {
  color: #718096;
  font-size: 0.85rem;
}

.transaction-note {
  color: #4a5568;
  font-size: 0.85rem;
  font-style: italic;
}

/* Transaction Values */
.transaction-values {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  text-align: right;
}

.transaction-price,
.transaction-total,
.transaction-profit {
  display: flex;
  justify-content: space-between;
  gap: 1rem;
}

.transaction-values .label {
  color: #718096;
  font-size: 0.85rem;
  font-weight: 500;
}

.transaction-values .value {
  font-weight: 700;
  color: #2d3748;
}

.transaction-total .value.buy {
  color: #f56565;
}

.transaction-total .value.sell {
  color: #48bb78;
}

.transaction-profit .value.positive {
  color: #48bb78;
}

.transaction-profit .value.negative {
  color: #f56565;
}

/* Responsive */
@media (max-width: 768px) {
  .history-header {
    flex-direction: column;
    align-items: flex-start;
  }

  .filter-buttons {
    width: 100%;
    justify-content: space-between;
  }

  .filter-buttons button {
    flex: 1;
    padding: 0.5rem 0.5rem;
    font-size: 0.8rem;
  }

  .transaction-item {
    grid-template-columns: 1fr;
    gap: 1rem;
  }

  .transaction-icon {
    display: none;
  }

  .transaction-values {
    text-align: left;
  }

  .transaction-price,
  .transaction-total,
  .transaction-profit {
    justify-content: space-between;
  }
}