/* src/pages/Portfolio.css */
.portfolio-page {
  max-width: 1400px;
  margin: 0 auto;
  padding: 2rem;
}

.portfolio-header {
  margin-bottom: 2rem;
}

.portfolio-header h1 {
  color: #2d3748;
  font-size: 2rem;
  margin: 0;
}

/* Portfolio Content Layout */
.portfolio-content {
  display: grid;
  grid-template-columns: 400px 1fr;
  gap: 2rem;
  margin-top: 2rem;
}

/* Transaction Section */
.transaction-section {
  position: sticky;
  top: 2rem;
  height: fit-content;
}

/* Portfolio Data Section */
.portfolio-data {
  min-height: 400px;
}

/* Tabs */
.tabs {
  display: flex;
  gap: 1rem;
  margin-bottom: 1.5rem;
  border-bottom: 2px solid #e2e8f0;
}

.tabs button {
  padding: 1rem 2rem;
  border: none;
  background: transparent;
  color: #718096;
  font-weight: 600;
  font-size: 1rem;
  cursor: pointer;
  position: relative;
  transition: all 0.3s ease;
}

.tabs button:hover {
  color: #4a5568;
}

.tabs button.active {
  color: #667eea;
}

.tabs button.active::after {
  content: '';
  position: absolute;
  bottom: -2px;
  left: 0;
  right: 0;
  height: 3px;
  background: #667eea;
  border-radius: 3px 3px 0 0;
}

/* Tab Content */
.tab-content {
  animation: fadeIn 0.3s ease;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Responsive */
@media (max-width: 1200px) {
  .portfolio-content {
    grid-template-columns: 350px 1fr;
    gap: 1.5rem;
  }
}

@media (max-width: 992px) {
  .portfolio-content {
    grid-template-columns: 1fr;
  }

  .transaction-section {
    position: static;
  }
}

@media (max-width: 768px) {
  .portfolio-page {
    padding: 1rem;
  }

  .portfolio-header h1 {
    font-size: 1.5rem;
  }

  .tabs {
    gap: 0.5rem;
  }

  .tabs button {
    padding: 0.75rem 1rem;
    font-size: 0.9rem;
  }
}