/* src/components/common/SearchableSelect.css */
.searchable-select {
  position: relative;
}

.select-input {
  padding: 0.75rem;
  border: 2px solid #e2e8f0;
  border-radius: 8px;
  background: white;
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: border-color 0.3s ease;
}

.select-input:hover {
  border-color: #cbd5e0;
}

.selected-option {
  display: flex;
  align-items: center;
  gap: 0.75rem;
}

.selected-option img {
  width: 24px;
  height: 24px;
}

.placeholder {
  color: #a0aec0;
}

.arrow {
  color: #718096;
  font-size: 0.75rem;
}

/* Dropdown */
.dropdown {
  position: absolute;
  top: calc(100% + 0.5rem);
  left: 0;
  right: 0;
  background: white;
  border: 2px solid #e2e8f0;
  border-radius: 8px;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.1);
  z-index: 1000;
  max-height: 400px;
  overflow: hidden;
}

.search-input {
  width: 100%;
  padding: 0.75rem;
  border: none;
  border-bottom: 2px solid #e2e8f0;
  font-size: 1rem;
}

.search-input:focus {
  outline: none;
  border-bottom-color: #667eea;
}

.options-list {
  max-height: 320px;
  overflow-y: auto;
}

.option {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding: 0.75rem;
  cursor: pointer;
  transition: background 0.2s ease;
}

.option:hover {
  background: #f7fafc;
}

.option img {
  width: 32px;
  height: 32px;
}

.option-info {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.option-name {
  font-weight: 600;
  color: #2d3748;
}

.option-symbol {
  font-size: 0.85rem;
  color: #718096;
}

.option-price {
  font-weight: 700;
  color: #2d3748;
}

.no-options {
  padding: 2rem;
  text-align: center;
  color: #a0aec0;
}