/* src/components/portfolio/HoldingsList.css */
.holdings-list {
  background: white;
  border-radius: 12px;
  padding: 1.5rem;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.holdings-list.empty {
  padding: 3rem;
}

.empty-state {
  text-align: center;
  color: #718096;
}

.empty-icon {
  font-size: 4rem;
  margin-bottom: 1rem;
}

.empty-state h3 {
  color: #2d3748;
  margin-bottom: 0.5rem;
}

.holdings-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
  padding-bottom: 1rem;
  border-bottom: 2px solid #e2e8f0;
}

.holdings-header h3 {
  color: #2d3748;
  font-size: 1.3rem;
  margin: 0;
}

.holdings-count {
  color: #718096;
  font-weight: 600;
}

/* Table */
.holdings-table {
  overflow-x: auto;
}

.table-header {
  display: grid;
  grid-template-columns: 2fr 1.5fr 1.2fr 1.2fr 1.5fr 1.5fr;
  gap: 1rem;
  padding: 0.75rem 1rem;
  background: #f7fafc;
  border-radius: 8px;
  font-weight: 600;
  font-size: 0.85rem;
  color: #4a5568;
  text-transform: uppercase;
  margin-bottom: 0.5rem;
}

.table-body {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.table-row {
  display: grid;
  grid-template-columns: 2fr 1.5fr 1.2fr 1.2fr 1.5fr 1.5fr;
  gap: 1rem;
  padding: 1rem;
  border-radius: 8px;
  transition: all 0.3s ease;
  align-items: center;
}

.table-row:hover {
  background: #f7fafc;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
}

/* Columns */
.col-coin {
  display: flex;
  align-items: center;
  gap: 0.75rem;
}

.col-coin img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}

.coin-details {
  display: flex;
  flex-direction: column;
}

.coin-name {
  font-weight: 600;
  color: #2d3748;
  font-size: 1rem;
}

.coin-symbol {
  font-size: 0.85rem;
  color: #718096;
}

.col-amount,
.col-price,
.col-value,
.col-profit {
  display: flex;
  flex-direction: column;
}

.amount-value {
  font-weight: 600;
  color: #2d3748;
}

.value-primary {
  font-weight: 700;
  color: #2d3748;
  font-size: 1.05rem;
}

.value-invested {
  font-size: 0.8rem;
  color: #718096;
  margin-top: 0.25rem;
}

.profit-value {
  font-weight: 700;
  font-size: 1.05rem;
}

.profit-percent {
  font-size: 0.85rem;
  font-weight: 600;
  margin-top: 0.25rem;
}

.positive {
  color: #48bb78;
}

.negative {
  color: #f56565;
}

/* Responsive */
@media (max-width: 1024px) {
  .table-header,
  .table-row {
    grid-template-columns: 2fr 1.2fr 1fr 1fr 1.3fr 1.3fr;
    gap: 0.5rem;
    font-size: 0.9rem;
  }

  .col-coin img {
    width: 32px;
    height: 32px;
  }
}

@media (max-width: 768px) {
  .table-header {
    display: none;
  }

  .table-row {
    grid-template-columns: 1fr;
    gap: 0.75rem;
    padding: 1.5rem;
    border: 1px solid #e2e8f0;
  }

  .col-coin,
  .col-amount,
  .col-price,
  .col-value,
  .col-profit {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }

  .col-coin::before,
  .col-amount::before,
  .col-price::before,
  .col-value::before,
  .col-profit::before {
    content: attr(data-label);
    font-weight: 600;
    color: #718096;
    font-size: 0.85rem;
  }
}