.markets-page {
  padding: 2rem;
  max-width: 1400px;
  margin: 0 auto;
  animation: fadeIn 0.3s ease-in;
}

/* Header */
.markets-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
  padding-bottom: 1.5rem;
  border-bottom: 2px solid #e0e0e0;
}

.header-content h1 {
  font-size: 2.5rem;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text; /* ✅ แก้ไข: เพิ่มบรรทัดนี้ */
  margin-bottom: 0.5rem;
}

.subtitle {
  color: #666;
  font-size: 1rem;
}

.refresh-indicator {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: #667eea;
  font-size: 0.9rem;
}

/* Controls */
.markets-controls {
  display: flex;
  gap: 1rem;
  margin-bottom: 2rem;
  flex-wrap: wrap;
  align-items: center;
}

/* Search */
.search-container {
  position: relative;
  flex: 1;
  min-width: 250px;
}

.search-input {
  width: 100%;
  padding: 0.75rem 1rem 0.75rem 2.5rem;
  border: 2px solid #e0e0e0;
  border-radius: 12px;
  font-size: 1rem;
  transition: all 0.3s ease;
}

.search-input:focus {
  outline: none;
  border-color: #667eea;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
}

.search-icon {
  position: absolute;
  left: 1rem;
  top: 50%;
  transform: translateY(-50%);
  font-size: 1.2rem;
}

/* Filter Buttons */
.filter-buttons {
  display: flex;
  gap: 0.5rem;
}

.filter-btn {
  padding: 0.75rem 1.5rem;
  border: 2px solid #e0e0e0;
  border-radius: 12px;
  background: white;
  font-size: 0.95rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
}

.filter-btn:hover {
  border-color: #667eea;
  color: #667eea;
}

.filter-btn.active {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border-color: transparent;
}

/* Results Count */
.results-count {
  color: #666;
  font-size: 0.95rem;
  padding: 0.75rem 1rem;
  background: #f5f5f5;
  border-radius: 12px;
}

/* Table - Desktop */
.markets-table-container {
  background: white;
  border-radius: 16px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
  overflow: hidden;
  margin-bottom: 2rem;
}

.markets-table {
  width: 100%;
  border-collapse: collapse;
}

.markets-table thead {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
}

.markets-table th {
  padding: 1rem;
  text-align: left;
  font-weight: 600;
  font-size: 0.95rem;
  cursor: pointer;
  user-select: none;
  white-space: nowrap;
}

.markets-table th:hover {
  background: rgba(255, 255, 255, 0.1);
}

.markets-table .text-right {
  text-align: right;
}

.sort-icon {
  margin-left: 0.5rem;
  opacity: 0.5;
  font-size: 0.8rem;
}

.sort-icon.active {
  opacity: 1;
}

.markets-table tbody tr {
  border-bottom: 1px solid #f0f0f0;
  transition: background 0.2s ease;
}

.markets-table tbody tr:hover {
  background: #f8f9ff;
}

.markets-table td {
  padding: 1rem;
  font-size: 0.95rem;
}

/* Coin Info */
.coin-info {
  display: flex;
  align-items: center;
  gap: 0.75rem;
}

.coin-image {
  width: 32px;
  height: 32px;
  border-radius: 50%;
}

.coin-names {
  display: flex;
  flex-direction: column;
}

.coin-name {
  font-weight: 600;
  color: #333;
}

.coin-symbol {
  font-size: 0.85rem;
  color: #999;
  text-transform: uppercase;
}

/* Rank */
.rank {
  color: #999;
  font-weight: 600;
}

/* Price */
.price {
  font-weight: 600;
  color: #333;
}

/* Change */
.change {
  font-weight: 600;
  padding: 0.25rem 0.5rem;
  border-radius: 6px;
}

.change.positive {
  color: #10b981;
  background: rgba(16, 185, 129, 0.1);
}

.change.negative {
  color: #ef4444;
  background: rgba(239, 68, 68, 0.1);
}

/* Market Cap & Volume */
.market-cap, .volume {
  color: #666;
}

/* Cards - Mobile */
.markets-cards {
  display: none;
}

.coin-card {
  background: white;
  border-radius: 16px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
  padding: 1.5rem;
  margin-bottom: 1rem;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.coin-card:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 25px rgba(0, 0, 0, 0.12);
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid #f0f0f0;
}

.card-body {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.card-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.card-row .label {
  color: #999;
  font-size: 0.9rem;
}

.card-row .value {
  font-weight: 600;
  color: #333;
}

/* Pagination */
.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  margin-top: 2rem;
}

.page-btn {
  padding: 0.75rem 1.5rem;
  border: 2px solid #e0e0e0;
  border-radius: 12px;
  background: white;
  font-size: 0.95rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
}

.page-btn:hover:not(:disabled) {
  border-color: #667eea;
  color: #667eea;
}

.page-btn:disabled {
  opacity: 0.4;
  cursor: not-allowed;
}

.page-numbers {
  display: flex;
  gap: 0.5rem;
  align-items: center;
}

.page-number {
  width: 40px;
  height: 40px;
  border: 2px solid #e0e0e0;
  border-radius: 8px;
  background: white;
  font-size: 0.95rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
}

.page-number:hover {
  border-color: #667eea;
  color: #667eea;
}

.page-number.active {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border-color: transparent;
}

.ellipsis {
  color: #999;
  padding: 0 0.5rem;
}

/* Loading & Error States */
.loading-container,
.error-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 400px;
  gap: 1rem;
}

.spinner {
  width: 50px;
  height: 50px;
  border: 4px solid #f0f0f0;
  border-top: 4px solid #667eea;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

.spinner-small {
  width: 20px;
  height: 20px;
  border: 3px solid #f0f0f0;
  border-top: 3px solid #667eea;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.error-message {
  color: #ef4444;
  font-size: 1.1rem;
  font-weight: 500;
}

.retry-button {
  padding: 0.75rem 2rem;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border: none;
  border-radius: 12px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.retry-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(102, 126, 234, 0.4);
}

/* Animations */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Responsive Design */
.desktop-only {
  display: block;
}

.mobile-only {
  display: none;
}

/* Tablet */
@media (max-width: 1024px) {
  .markets-page {
    padding: 1.5rem;
  }

  .header-content h1 {
    font-size: 2rem;
  }

  .markets-table {
    font-size: 0.9rem;
  }

  .markets-table th,
  .markets-table td {
    padding: 0.75rem 0.5rem;
  }

  .coin-image {
    width: 28px;
    height: 28px;
  }
}

/* Mobile */
@media (max-width: 768px) {
  .markets-page {
    padding: 1rem;
  }

  .markets-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 1rem;
  }

  .header-content h1 {
    font-size: 1.75rem;
  }

  .subtitle {
    font-size: 0.9rem;
  }

  .markets-controls {
    flex-direction: column;
    gap: 1rem;
  }

  .search-container {
    width: 100%;
    min-width: auto;
  }

  .filter-buttons {
    width: 100%;
    justify-content: space-between;
  }

  .filter-btn {
    flex: 1;
    padding: 0.75rem 1rem;
    font-size: 0.85rem;
  }

  .results-count {
    width: 100%;
    text-align: center;
  }

  /* Hide table, show cards */
  .desktop-only {
    display: none;
  }

  .mobile-only {
    display: block;
  }

  /* Pagination mobile */
  .pagination {
    flex-wrap: wrap;
    gap: 0.5rem;
  }

  .page-btn {
    padding: 0.5rem 1rem;
    font-size: 0.85rem;
  }

  .page-number {
    width: 35px;
    height: 35px;
    font-size: 0.85rem;
  }
}

/* Small Mobile */
@media (max-width: 480px) {
  .markets-page {
    padding: 0.75rem;
  }

  .header-content h1 {
    font-size: 1.5rem;
  }

  .coin-card {
    padding: 1rem;
  }

  .filter-btn {
    padding: 0.5rem 0.75rem;
    font-size: 0.8rem;
  }

  .page-numbers {
    gap: 0.25rem;
  }

  .page-number {
    width: 30px;
    height: 30px;
    font-size: 0.8rem;
  }
}