/* src/pages/Dashboard.css */
.dashboard {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
}

.dashboard h1 {
  color: #2d3748;
  margin-bottom: 2rem;
  font-size: 2rem;
}

.loading {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 50vh;
  font-size: 1.5rem;
  color: #667eea;
}

/* Summary Cards */
.summary-cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1.5rem;
  margin-bottom: 3rem;
}

.card {
  background: white;
  padding: 1.5rem;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.card:hover {
  transform: translateY(-5px);
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.15);
}

.card h3 {
  color: #718096;
  font-size: 0.9rem;
  font-weight: 500;
  margin-bottom: 0.5rem;
  text-transform: uppercase;
}

.card .value {
  color: #2d3748;
  font-size: 1.8rem;
  font-weight: 700;
  margin: 0;
}

.card .value.positive {
  color: #48bb78;
}

.card .value.negative {
  color: #f56565;
}

.card .roi {
  color: #718096;
  font-size: 0.9rem;
  margin-top: 0.5rem;
}

/* Top Coins */
.top-coins {
  margin-top: 3rem;
}

.top-coins h2 {
  color: #2d3748;
  margin-bottom: 1.5rem;
  font-size: 1.5rem;
}

.coins-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 1.5rem;
}

.coin-card {
  background: white;
  padding: 1.5rem;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.coin-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.15);
}

.coin-card img {
  width: 48px;
  height: 48px;
  margin-bottom: 1rem;
}

.coin-card h3 {
  color: #2d3748;
  font-size: 1.1rem;
  margin-bottom: 0.25rem;
}

.coin-card .symbol {
  color: #a0aec0;
  font-size: 0.85rem;
  margin-bottom: 0.5rem;
}

.coin-card .price {
  color: #2d3748;
  font-size: 1.3rem;
  font-weight: 700;
  margin: 0.5rem 0;
}

.coin-card .change {
  font-size: 0.9rem;
  font-weight: 600;
  padding: 0.25rem 0.5rem;
  border-radius: 6px;
  display: inline-block;
}

.coin-card .change.positive {
  color: #48bb78;
  background: rgba(72, 187, 120, 0.1);
}

.coin-card .change.negative {
  color: #f56565;
  background: rgba(245, 101, 101, 0.1);
}

/* Responsive */
@media (max-width: 768px) {
  .dashboard {
    padding: 1rem;
  }

  .dashboard h1 {
    font-size: 1.5rem;
  }

  .summary-cards {
    grid-template-columns: 1fr;
  }

  .coins-grid {
    grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
  }
}