/* src/components/portfolio/TransactionForm.css */
.transaction-form {
  background: white;
  padding: 2rem;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.transaction-form h2 {
  color: #2d3748;
  margin-bottom: 1.5rem;
  font-size: 1.5rem;
}

/* Transaction Type Toggle */
.transaction-type-toggle {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 0.5rem;
  margin-bottom: 1.5rem;
}

.transaction-type-toggle button {
  padding: 0.75rem;
  border: 2px solid #e2e8f0;
  background: white;
  color: #718096;
  border-radius: 8px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.transaction-type-toggle button:hover {
  border-color: #cbd5e0;
}

.transaction-type-toggle button.buy.active {
  background: #48bb78;
  color: white;
  border-color: #48bb78;
}

.transaction-type-toggle button.sell.active {
  background: #f56565;
  color: white;
  border-color: #f56565;
}

/* Form Groups */
.form-group {
  margin-bottom: 1.5rem;
}

.form-group label {
  display: block;
  color: #4a5568;
  font-weight: 600;
  margin-bottom: 0.5rem;
  font-size: 0.9rem;
}

.form-group input,
.form-group select {
  width: 100%;
  padding: 0.75rem;
  border: 2px solid #e2e8f0;
  border-radius: 8px;
  font-size: 1rem;
  transition: border-color 0.3s ease;
}

.form-group input:focus,
.form-group select:focus {
  outline: none;
  border-color: #667eea;
}

.coin-info {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  margin-top: 0.75rem;
  padding: 0.75rem;
  background: #f7fafc;
  border-radius: 8px;
}

.coin-info img {
  width: 32px;
  height: 32px;
}

.coin-info .price {
  margin-left: auto;
  font-weight: 700;
  color: #2d3748;
}

.help-text {
  margin-top: 0.5rem;
  font-size: 0.85rem;
  color: #718096;
}

/* Transaction Summary */
.transaction-summary {
  background: #f7fafc;
  padding: 1rem;
  border-radius: 8px;
  margin-bottom: 1rem;
}

.summary-row {
  display: flex;
  justify-content: space-between;
  padding: 0.5rem 0;
  color: #4a5568;
}

.summary-row.total {
  border-top: 2px solid #e2e8f0;
  margin-top: 0.5rem;
  padding-top: 0.75rem;
  font-weight: 700;
  font-size: 1.1rem;
  color: #2d3748;
}

/* Message */
.message {
  padding: 1rem;
  border-radius: 8px;
  margin-bottom: 1rem;
  font-weight: 500;
}

.message.success {
  background: #c6f6d5;
  color: #22543d;
  border: 1px solid #48bb78;
}

.message.error {
  background: #fed7d7;
  color: #742a2a;
  border: 1px solid #f56565;
}

/* Submit Button */
.submit-btn {
  width: 100%;
  padding: 1rem;
  border: none;
  border-radius: 8px;
  font-size: 1.1rem;
  font-weight: 700;
  cursor: pointer;
  transition: all 0.3s ease;
  color: white;
}

.submit-btn.buy {
  background: #48bb78;
}

.submit-btn.buy:hover {
  background: #38a169;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(72, 187, 120, 0.4);
}

.submit-btn.sell {
  background: #f56565;
}

.submit-btn.sell:hover {
  background: #e53e3e;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(245, 101, 101, 0.4);
}

/* Available Cash */
.available-cash {
  margin-top: 1rem;
  padding: 0.75rem;
  background: #edf2f7;
  border-radius: 8px;
  text-align: center;
  font-weight: 600;
  color: #2d3748;
}

/* Responsive */
@media (max-width: 768px) {
  .transaction-form {
    padding: 1.5rem;
  }
}